import nodeMailerTransporter from '../../config/node_mailer';

export default async function sendMailToUsers(email, subject, text, html) {
  try {
    const transporter = await nodeMailerTransporter();
    const mailStatus = await transporter.sendMail({
      from: 'realgov.test@gmail.com',
      to: email,
      subject,
      text,
      html,
    });
    return mailStatus;
  } catch (err) {
    console.log(err);
    return err;
  }
}
